@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;500;600;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@charset "utf-8";
/* CSS Document */

/* 
html5doctor.com Reset Stylesheet
v1.6.1
Last Updated: 2010-09-17
Author: Richard Clark - http://richclarkdesign.com 
Twitter: @rich_clark
*/

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i, p, 
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
    margin:0;
    padding:0;
    border:0;
    outline:0;
    font-size:100%;
    background:transparent;
}

body {
    line-height:1;
}

article,aside,details,figcaption,figure,
footer,header,hgroup,menu,nav,section { 
    display:block;
}

nav ul {
    list-style:none;
}

ol,ul,li{list-style:none;}


blockquote, q {
    quotes:none;
}

blockquote:before, blockquote:after,
q:before, q:after {
    content:'';
    content:none;
}

a {
    margin:0;
    padding:0;
    font-size:100%;
    vertical-align:baseline;
    background:transparent;
	text-decoration:none;
}

/* change colours to suit your needs */
ins {
    background-color:#ff9;
    color:#000;
    text-decoration:none;
}

/* change colours to suit your needs */
mark {
    background-color:#ff9;
    color:#000; 
    font-style:italic;
    font-weight:bold;
}

del {
    text-decoration: line-through;
}

abbr[title], dfn[title] {
    border-bottom:1px dotted;
    cursor:help;
}

table {
    border-collapse:collapse;
    border-spacing:0;
}

/* change border colour to suit your needs */
hr {
    display:block;
    height:1px;
    border:0;   
    border-top:1px solid #cccccc;
    margin:1em 0;
    padding:0;
}

input, select {
    vertical-align:middle;
}


@charset "utf-8";


*{ box-sizing: border-box; }

html,body{ margin:0; padding:0; width:100%; height:100%;}
body{ font-family: 'Montserrat', sans-serif; }
a{ color:#333; }
a:hover{ font-weight:bold; }
.container{ margin:0 auto; width:1200px; }

.flex{ display:flex; }
.flex-around{ justify-content: space-between; }
.flex-center{ justify-content:center; }


.header{ background:#000; }
.header h1{ padding:20px 30px; }
.header h1 a{ font-size:2em; font-weight:700; color:#fff; }
.ListBox{ position:relative; width:100%; border-bottom:1px solid #ddd; margin:10px 0; padding:30px; }
.ListBox dt{ font-size:1.3em; font-weight:600; color:#333;padding-bottom:10px; } 
.ListBox dd{ color:#666; padding-bottom:30px; letter-spacing:-1.5px;  } 
.ListBox > p > .date { font-size:0.9em; } 
.emojiWrap{ display:flex; float:right; align-items: center; }
.emoji{ animation: 요리조리 1.5s infinite; padding:0 10px; cursor:pointer; }
.Remove-btn{ position:absolute; top:30px; right:0; }
.Remove-btn a{ padding:5px 10px; border:2px solid #333; font-size:0.8em; cursor:pointer; border-radius: 20px;}

.ListCreateBox{ margin-top:60px; padding:30px; width:100%; }
.ListCreateBox input{ width:100%; border:none; border-bottom:1px solid #ccc; margin-bottom:30px; }
.ListCreateBox label{ display:block; font-weight:bold; padding:10px 0; }
.submit{ padding-top:30px; text-align:center; }
.submit button{ border:1px solid #333; padding:10px 30px; background:#333; color:#fff; border-radius: 20px; }

@media (max-width: 1200px){
  .container{ width:100%; padding:0 30px; }
}




@keyframes 요리조리 {
  0% {
    transform: rotate(0deg);
  }

  10% {
    transform: rotate(-8deg);
  }

  20% {
    transform: rotate(8deg);
  }
  
  30% {
    transform: rotate(-8deg);
  }

  40% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes bounce {
  100% {
    top: -20px;
    text-shadow: 0 1px 0 #CCC,
                 0 2px 0 #CCC,
                 0 3px 0 #CCC,
                 0 4px 0 #CCC,
                 0 5px 0 #CCC,
                 0 6px 0 #CCC,
                 0 7px 0 #CCC,
                 0 8px 0 #CCC,
                 0 9px 0 #CCC,
                 0 50px 25px rgba(0, 0, 0, .2);
  }
}

@keyframes titleAnimation {
    0% {
      transform: translateY(-50px);
      opacity: 0;
      -webkit-clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 80%);
      clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 80%);
  }
  20% {
      transform: translateY(0);
      opacity: 1;
      -webkit-clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 15%);
      clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 15%);
  }
  80% {
       transform: translateY(0);
       opacity: 1;
      -webkit-clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 15%);
      clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 15%);
  }
  100% {
       transform: translateY(50px);
       opacity: 0;
      -webkit-clip-path: polygon(100% 0, 100% -0%, 0 100%, 0 100%);
      clip-path: polygon(100% 0, 100% -0%, 0 100%, 0 100%);
    
  }
}
